import { FormControlType, FormErrorType } from 'src/core/enums/form.enum';
import { FormControl } from 'src/core/models/form';
import styled from 'styled-components';

const Warning = styled.div`
  color: red;
  font-size: 12px;
`;

export default function FormErrorMsg(props: { errMsg: FormControl; name: FormControlType }) {
  const { errMsg, name } = props;

  return (
    <>
      {errMsg[name]?.[FormErrorType.Format] && <Warning></Warning>}
      {errMsg[name]?.[FormErrorType.Required] && <Warning></Warning>}
      {errMsg[name]?.[FormErrorType.Integer] && <Warning></Warning>}
      {errMsg[name]?.[FormErrorType.Auth] && <Warning></Warning>}
      {errMsg[name]?.[FormErrorType.Account] && <Warning></Warning>}
    </>
  );
}
